<template>
  <div class="address-content">
    <V-Scroll :isList="false" :scroll-height="`${mainHeight}px`">
      <div class="add-address">
        <p>新增收货地址</p>
      </div>
      <ul class="address-list">
        <li>
          <div>
            <p></p>
          </div>
          <div>
            <p>一个名字</p>
            <p>这是一个很长的地址比如：和的霍华德和的很多很多和</p>
          </div>
          <div>
            <p></p>
          </div>
        </li>
      </ul>
    </V-Scroll>
  </div>
</template>

<script>
import Ajax from "@/mixins/ajax";

export default {
  name: "orderList",
  data() {
    return {
    };
  },
  mixins: [Ajax],
  components: {},
  mounted() {
  },
  methods: {}
};
</script>

<style scoped>
  .add-address{
    height: 1.48rem;
    line-height: 1.48rem;
    background-color: #fff;
    padding:0 .3rem;
    margin:.3rem auto;
  }
  .add-address >p {
    height: 1.48rem;
    font-size: .48rem;
    line-height: 1.48rem;
    color: #353535;
    padding-left:.6rem;
    background: url("../../images/userInfos/icon_add.png")no-repeat left center/.48rem .48rem,
                url("../../images/my/icon_right.png")no-repeat right center/.17rem .36rem;
  }
  .address-list li{
    height: 2.46rem;
    background-color: #fff;
    margin-bottom:.3rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding:0 .3rem;
  }
  .address-list li> div:nth-child(1)>p{
    width:1rem;
    height: 1rem;
    background: url("../../images/userInfos/icon_addChecked.png") no-repeat center/.44rem .36rem;
  }
  .address-list li > div:nth-child(2){
    margin:0 .1rem;
  }
  .address-list li > div:nth-child(2)>p:nth-child(1){
    height: .6rem;
    font-size: .48rem;
    line-height: .6rem;
    color: #353535;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 1;
    overflow: hidden;
  }
  .address-list li > div:nth-child(2)>p:nth-child(2){
    height: 1rem;
    font-size: .42rem;
    line-height: .5rem;
    color: #666666;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    overflow: hidden;
  }
  .address-list li> div:nth-child(3)>p{
    width:1rem;
    height:1rem;
    background: url("../../images/userInfos/icon_address.png") no-repeat center/.48rem .48rem;
  }
</style>



